<template>
<div class="page infopage">
  <section class="section showonload">
    <div class="container">
      <h1>Отзывы</h1>
      <div v-for='feedbackItem in feedback'
           :key='feedbackItem.id'
           class="feedback-feed">
        <div class="row row-main feedback-feed-item">
          <div class="col col-m-3 col-l-2">
            <img :src="`${ require('../assets/images/feedback/feedback-person-image-' + feedbackItem.id + '.jpg') }`"
                 class="feedback-feed-photo" alt="">
          </div>
          <div class="col col-m-9 col-l-10">
            <h3>{{ feedbackItem.name }}</h3>
            <div class="feedback-feed-about">
              {{ feedbackItem.about }}
            </div>
            <p>
              {{ feedbackItem.text }}
            </p>
          </div>
        </div>
      </div>

      <h2 class="conclusion">
        Приятных покупок!
      </h2>
    </div>
  </section>

  <transition name="fade">
    <div v-if="!ifPageLoaded">
      <pulse-loader :loading="loading" :color="'#546b84'" :size="'12px'"
                    class="loading-spinner"></pulse-loader>
    </div>
  </transition>

</div>
</template>


<script>
import * as Feedback from './services/feedback.js';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Product',
  props: ['pageLoaded'],
  components: {
    PulseLoader
  },
  data () {
    return {
      loading: true,
      feedback: Feedback.feedback
    }
  },
  mounted () {
    this.scrollToTop()
    this.loading = false
  },
  computed: {
    ifPageLoaded: function() {
      return this.pageLoaded === true
    }
  }
}
</script>



