<template>
<main class="page product-group-page">
  <section class="section showonload">
    <div class="container products">

      <h1>{{ productGroup.groupName }}</h1>
      <div class="breadcrumbs">
        <span><</span>
        <router-link :to="{ path: '/'}">Каталог</router-link>
      </div>
      <div class="row row-main">
        <a v-for='product in productGroup.products' @click='goToProduct(product)'
                  :key='product.id'
                  :style="{ backgroundImage: 'url(' + `${require('../assets/images/groups/' + groupNumber + '/product-' + product.id + '.jpg')}` + ')' }"
                  class="product-card">
          <div class="product-card-inner">
            <div class="name">{{ product.name }}</div>
          </div>
        </a>
      </div>
    </div>
  </section>

  <!-- <section class="section section-products-recommended showonload">
    <div class="container">
      <h2 class="section-title">Рекомендуемые товары</h2>
      <div class="row row-main">
        <router-link v-for='product in product.recommended'
                     :key='product.id'
                     :to="{ path: '/product/' + product.productId }"
                     class="col col-l-3">
          <div :style="{ backgroundImage: 'url(' + `${require('../assets/images/products/' + product.productId + '/product-image-1.jpg')}` + ')' }"
               class="product-recommended-photo">
          </div>
          <div class="product-recommended-name">
            {{ products[product.productId - 1].name }}
          </div>
          <div class="product-recommended-price">
            {{ products[product.productId - 1].sizes[0].price }}
          </div>
        </router-link>
      </div>
    </div>
  </section> -->

  <transition name="fade">
    <div v-if="!ifPageLoaded">
      <pulse-loader :loading="loading" :color="'#546b84'" :size="'12px'"
                    class="loading-spinner"></pulse-loader>
    </div>
  </transition>

</main>
</template>


<script>
import * as Products from './services/products.js';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'ProductGroup',
  props: ['pageLoaded'],
  components: {
    PulseLoader
  },
  data () {
    return {
      loading: true,
      groupNumber: Products.productGroups[this.$route.params.id]
                   ? String(Number(this.$route.params.id) + 1)
                   : 1,
      productGroup: Products.productGroups[this.$route.params.id]
               ? Products.productGroups[this.$route.params.id]
               : Products.productGroups[0],
      products: Products.products
    }
  },
  mounted () {
    this.scrollToTop()
  },
  methods: {
    goToProduct(product) {
      if (this.products[product.id - 1].id !== undefined) {
        this.$router.push('/product/' + (product.id - 1))
      } else {
        this.$router.push('/page-under-development')
      }
    }
  },
  computed: {
    ifPageLoaded: function() {
      return this.pageLoaded === true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>



