      </a>
      <br><br>
      <img :src="`${ require('../assets/images/instagram-feed-fake.jpg') }`"
           alt="Instagram photos"
           class="full-width-img">

      <!-- <vue-instagram token="1642281659.07462c1.b66d639259d049d08a955988b7449b57"
                     username="dobraya_ovechka"
                     :count="4" :tags="['hashtag1', 'hashtag2']">
