    <div v-if="!ifPageLoaded">
      <pulse-loader :loading="loading" :color="'#546b84'" :size="'12px'"
                    class="loading-spinner"></pulse-loader>
    </div>
  </transition>

</main>
</template>


<script>
import * as Products from './services/products.js'
import { Carousel, Slide } from 'vue-carousel'
import VueInstagram from 'vue-instagram'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Homepage',
  props: ['pageLoaded'],
  components: {
    Carousel,
    Slide,
    VueInstagram,
    PulseLoader
  },
  data () {
    return {
      products: Products.catalog,
      feedbackPopoverOpened: false,
      loading: true
    }
  },
  mounted () {
    this.scrollToTop()
  },
  methods: {
    feedbackPopoverOpen() {
      this.feedbackPopoverOpened = !this.feedbackPopoverOpened
    },
    goToProduct(product) {
      if (product.standalone) {
        this.$router.push(product.productLink)
      } else this.$router.push('/products/' + (product.id - 1))
    }
  },
  computed: {
    ifPageLoaded: function() {
      return this.pageLoaded === true
    }
  }
}
</script>

