<template>
<main class="page product-group-page page-under-development">
  <section class="section showonload">
    <div class="container products">
      <br><br>
      <div class="row row-column">
        <h2>Страничка в разработке.</h2>
        <p>Очень скоро будет готова</p>
      </div>
      <div class="breadcrumbs">
        <router-link :to="{ path: '/'}">Каталог</router-link>
        <span><</span>
        <a href="javascript:history.go(-1)">Назад</a>
      </div>
    </div>
  </section>
</main>
</template>

<script>
export default {
  name: 'PageUnderDevelopment',
  mounted () {
    this.scrollToTop()
  }
}
</script>



