import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage'
import ProductGroup from '@/components/ProductGroup'
import Product from '@/components/Product'
import DeliveryPayment from '@/components/DeliveryPayment'
import Feedback from '@/components/Feedback'
import AboutUs from '@/components/AboutUs'
import Contacts from '@/components/Contacts'
import PageUnderDevelopment from '@/components/PageUnderDevelopment'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage
    },
    {
      path: '/products/:id',
      name: 'ProductGroup',
      component: ProductGroup
    },
    {
      path: '/product/:id',
      name: 'Product',
      component: Product
    },
    {
      path: '/delivery-payment',
      name: 'DeliveryPayment',
      component: DeliveryPayment
    },
    {
      path: '/about-us',
      name: 'AboutUs',
      component: AboutUs
    },
    {
      path: '/feedback',
      name: 'Feedback',
      component: Feedback
    },
    {
      path: '/contacts',
      name: 'Contacts',
      component: Contacts
    },
    {
      path: '/page-under-development',
      name: 'PageUnderDevelopment',
      component: PageUnderDevelopment
    }
  ]
})



