<template>
<main class="page product-page">
  <section class="section showonload">
    <div class="container">
      <div class="breadcrumbs">
        <router-link :to="{ path: '/'}">
          Каталог
        </router-link>
        <span>></span>
        <div v-if='product.groupId !== null'>
          <router-link :to="{ path: '/products/' + (product.groupId - 1)}">
            {{ product.groupName }}
          </router-link>
          <span>></span>
        </div>
        <span class="current">{{ product.name }}</span>
      </div>
      <div class="row row-main">
        <div :class="{ 'fullscreen-view': galleryFullscreen }"  class="col col-s-12 col-l-6 gallery">
          <div @click="galleryFullscreenToggle()" class="gallery-item-overlay"></div>
          <div @click="galleryFullscreenToggle()"
               :class="{ 'fullscreen': galleryFullscreen }"
               :style="{ backgroundImage: 'url(' + `${ require(
                    '../assets/images/products/' + product.id + '/product-image-' + product.galleryImageChosenNumber + '.jpg'
                  )}` + ')' }"
               class="gallery-item">
          </div>
          <div v-if='product.gallery.length > 1' class="gallery-items-mini">
            <div class="row">
              <div v-for='galleryItem in product.gallery' to="/product"
                   :key='galleryItem.id'
                   class="col col-l-4 col-s-4">
                <div :style="{ backgroundImage: 'url(' + `${ require(
                          '../assets/images/products/' + product.id + '/product-image-' + galleryItem.id + '.jpg'
                        )}` + ')' }"
                     @click="setGalleryImage(galleryItem.id)"
                     :class="{ 'active': product.galleryImageChosenNumber == galleryItem.id }"
                     class="gallery-item-mini">
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col col-s-12 col-l-6">
          <h2 class="product-name">{{ product.name }}</h2>

          <div>{{ product.composition }}</div>
          <div v-html='product.productionTime'></div>
          <div class="price">{{ productChosenSizePrice }}</div>

          <div v-if='productSizes.length === 1' class="text-select-row without-select">
            {{ productSize.sizeOnly }}
          </div>
          <div v-if='productSizes.length > 1' class="text-select-row">
            {{ productSize.size }}
            <select v-model='productSize'
                    @change='changeSize(productSize)'
                    class="text-select">
              <option v-for='size in productSizes' :key='size.size' :value='size'>
                {{ size.size }}
              </option>
            </select>
            <svg v-svg symbol="icon-select-arrow-desktop" size="50 48"
                 class="text-select-arrow desktop-only"></svg>
            <svg v-svg symbol="icon-select-arrow" size="38 38"
                 class="text-select-arrow mobile-only"></svg>
          </div>

          <div class="row palette">

            <div v-if='productColors.length <= 16'
                 v-for='color in productColors'
                 :key='color.id'
                 class="col col-s-3">
              <div @click="setPaletteColor(color.id)"
                   :class="{ 'active': product.chosenColor == color.id }"
                   class="palette-color-select">
                <img :src="`${ require('../assets/images/palette/' + productPaletteNumber + '/palette-color-' + color.id + '.jpg') }`"
                     class="palette-thumb">
                <div :class='{ "palette-name-dim": product.colorsPalette === 4 }'
                     class="palette-name">{{ color.name }}</div>
              </div>
            </div>

            <div v-if='productColors.length > 16'
                :class="{ 'opened': paletteOpened }"
                class="accordion accordion-palette">
              <div class="palette-row">
                <div v-for='color in firstTenColorsFromPalette'
                    :key='color.id'
                    class="col col-s-3">
                  <div @click="setPaletteColor(color.id)"
                      :class="{ 'active': product.chosenColor == color.id }"
                      class="palette-color-select">
                    <img :src="`${ require('../assets/images/palette/' + productPaletteNumber + '/palette-color-' + color.id + '.jpg') }`"
                        class="palette-thumb">
                    <div :class='{ "palette-name-dim": product.colorsPalette === 4 }'
                        class="palette-name">{{ color.name }}</div>
                  </div>
                </div>
              </div>

              <div @click="paletteOpened = !paletteOpened"
                   class="accordion-trigger palette-row-accordion-trigger">
                <span v-if='!paletteOpened' class="accordion-trigger-text">
                  Больше цветов
                </span>
                <span v-if='paletteOpened' class="accordion-trigger-text">
                  Меньше цветов
                </span>
                <span class="accordion-trigger-icon">
                  <svg v-svg symbol="icon-arrow-down" size="8 8"></svg>
                </span>
              </div>
              <div class="accordion-content row">
                <div v-for='color in restOfColorsFromPalette'
                    :key='color.id'
                    class="col col-s-3">
                  <div @click="setPaletteColor(color.id)"
                      :class="{ 'active': product.chosenColor == color.id }"
                      class="palette-color-select">
                    <img :src="`${ require('../assets/images/palette/' + productPaletteNumber + '/palette-color-' + color.id + '.jpg') }`"
                        class="palette-thumb">
                    <div :class='{ "palette-name-dim": product.colorsPalette === 4 }'
                        class="palette-name">{{ color.name }}</div>
                  </div>
                </div>
              </div>
              <div v-if='paletteOpened'
                    @click="paletteOpened = !paletteOpened; scrollToTop()"
                    class="accordion-trigger palette-row-accordion-trigger">
                <span class="accordion-trigger-text">
                  Меньше цветов
                </span>
                <span class="accordion-trigger-icon">
                  <svg v-svg symbol="icon-arrow-down" size="8 8"></svg>
                </span>
              </div><br>
            </div>
          </div>

          <div class="row palette pattern">
            <div v-for='pattern in productPatterns'
                 :key='pattern.id'
                 class="col col-l-4 col-s-4 col-xs-6">
              <div @click="setPatternColor(pattern.id)"
                   :class="{ 'active': product.chosenPattern == pattern.id }"
                   class="palette-color-select">
                <img :src="`${ require('../assets/images/pattern/' + productPatternNumber + '/pattern-type-' + pattern.id + '.jpg') }`"
                     class="palette-thumb pattern-thumb">
                <div class="palette-name">{{ pattern.name }}</div>
              </div>
            </div>
          </div>

          <div class="add-to-card">
            <input @click="submitOrder"
                   class="button button-primary section-button" value="Добавить в корзину">
            <div class="quantity">
              <input v-model="product.orderAmount"
                     type="number" class="quantity-value-field" min="1" max="10" step="1">
              <a @click="product.orderAmount--"
                 :class="{ 'disabled': product.orderAmount === 1}"
                 class="quantity-button quantity-button-down">
                -
              </a>
              <span v-html="product.orderAmount"
                    class="quantity-value">
                1
              </span>
              <a @click="product.orderAmount++"
                 :class="{ 'disabled': product.orderAmount === 10 }"
                 class="quantity-button quantity-button-up">
                +
              </a>
            </div>

            <img :src="order.photo" alt="">

          </div>

          <p class="detached" v-html="product.description"></p>

          <div class="desktop-only">
            <div class="tabs">
              <span v-for='tab in product.tabs'
                    :key='tab.id'
                    :class="{ 'active': product.chosenTab == tab.id }"
                    @click="setPageTab(tab.id)"
                    class="tab">
                <span class="tab-text">{{ tab.name }}</span>
              </span>
            </div>
            <div class="tabs-content">
              <div v-for='tabContent in product.tabs'
                  :key='tabContent.id'>
                <transition name="tab">
                  <div v-if='product.chosenTab == tabContent.id' v-html="tabContent.content"></div>
                </transition>
              </div>
            </div>
          </div>

          <div class="mobile-only">
            <div v-for='tab in product.tabs'
                 :key='tab.id'
                 :class="{ 'opened': product.chosenTab == tab.id }"
                 @click="setPageTab(tab.id)"
                 class="accordion">
              <div class="accordion-trigger">
                <span class="accordion-trigger-text">
                  {{ tab.name }}
                </span>
                <span class="accordion-trigger-icon">
                  <svg v-svg symbol="icon-arrow-down" size="8 8"></svg>
                </span>
              </div>
              <div v-if='product.chosenTab == tab.id'
                   v-html="tab.content"
                   class="accordion-content">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>

  <section class="section section-products-recommended showonload">
    <div class="container">
      <h2 class="section-title">Рекомендуемые товары</h2>
      <div class="row row-main">
        <a v-for='product in product.recommended'
                  :href='"/#/product/" + (product.productId - 1)'
                  :key='product.id'
                  class="col col-l-3">
          <div @click='goToRecommendedProduct(product)'
               :style="{ backgroundImage: 'url('
                       + `${require('../assets/images/products/'
                       + product.productId + '/product-image-1.jpg')}` + ')' }"
               class="product-recommended-photo">
          </div>
          <div class="product-recommended-name">
            {{ products[product.productId - 1].name }}
          </div>
          <div class="product-recommended-price">
            {{ products[product.productId - 1].sizes[0].price }}
          </div>
        </a>
      </div>
    </div>
  </section>

  <transition name="fade">
    <div v-if="!ifPageLoaded">
      <pulse-loader :loading="loading" :color="'#546b84'" :size="'12px'"
                    class="loading-spinner"></pulse-loader>
    </div>
  </transition>

</main>
</template>


<script>
import * as Products from './services/products.js';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Product',
  props: ['pageLoaded'],
  components: {
    PulseLoader
  },
  data () {
    return {
      loading: true,
      products: Products.products,
      product: Products.products[this.$route.params.id],
      chosenSize: '0',
      productSizes: Products.products[this.$route.params.id].sizes,
      productSize: Products.products[this.$route.params.id].sizes[0],
      productPaletteNumber: Products.products[this.$route.params.id].colorsPalette,
      productColors: Products.products[this.$route.params.id].colorsPalette !== null
                     ? Products.palette[Products.products[this.$route.params.id].colorsPalette - 1]
                     : [],
      productPatternNumber: Products.products[this.$route.params.id].pattern,
      productPatterns: Products.products[this.$route.params.id].patternType !== null
                      ? Products.pattern[Products.products[this.$route.params.id].pattern - 1]
                      : [],
      recommendedProductsNumbers: Products.products[this.$route.params.id].recommended,
      galleryFullscreen: false,
      order: {}
    }
  },
  mounted () {
    this.scrollToTop()
    setTimeout(function() {
      localStorage.removeItem('openedFromRecommended')
    }, 2000)
  },
  updated () {
    if (localStorage.getItem('openedFromRecommended')) {
      this.scrollToTop()
    }
  },
  methods: {
    setGalleryImage: function(chosenNumber) {
      this.product.galleryImageChosenNumber = chosenNumber
    },
    galleryFullscreenToggle: function() {
      this.scrollToTop()
      this.galleryFullscreen = !this.galleryFullscreen
    },
    setPageTab: function(chosenNumber) {
      this.product.chosenTab = chosenNumber
    },
    setPaletteColor: function(chosenColor) {
      this.product.chosenColor = chosenColor;
    },
    setPatternColor: function(chosenPattern) {
      this.product.chosenPattern = chosenPattern
    },
    changeSize: function(currentValue) {
      this.chosenSize = this.productSizes.indexOf(currentValue)
    },
    submitOrder: function() {
      this.order.productId = this.product.id
      this.order.name = this.product.name
      this.order.size = this.product.sizes[this.chosenSize].size
      if (this.productColors.length) {
        this.order.color = this.productColors[this.product.chosenColor - 1].name
      }
      if (this.productPatterns !== undefined) {
        this.order.pattern = this.productPatterns[this.product.chosenPattern - 1].name
      }
      this.order.amount = this.product.orderAmount
      this.order.price = this.product.orderAmount
                       * this.productSizes[this.chosenSize].priceVal
      this.order.priceInit = this.productSizes[this.chosenSize].priceVal
      this.order.photo = 'product-image-' + this.product.galleryImageChosenNumber + '.jpg'
      this.$emit('submitItemToBag', this.order)
      this.order = {}
    },
    goToRecommendedProduct(product) {
      if (this.products[product.productId - 1].id !== undefined) {
        localStorage.setItem('openedFromRecommended', true)
        let currentRoute = '/product/' + (product.productId - 1)
        this.$router.go(this.$router.currentRoute)
      } else this.$router.push('/page-under-development')
    }
  },
  computed: {
    productChosenSizePrice: function() {
      return this.productSizes[this.chosenSize].price
    },
    ifPageLoaded: function() {
      return this.pageLoaded === true
    },
    firstTenColorsFromPalette: function() {
      return this.productColors.slice(0, 5)
    },
    restOfColorsFromPalette: function() {
      return this.productColors.slice(6)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>



