<script>
import Firebase from 'firebase'
import toastr from 'toastr'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import OrdersBag from './components/OrdersBag'

let config = {
  apiKey: "AIzaSyBB4u0dqPzIzwvIJ3qz7MQFfiDbjFt369g",
  authDomain: "dobraya-ovechka.firebaseapp.com",
  databaseURL: "https://dobraya-ovechka.firebaseio.com",
  projectId: "dobraya-ovechka",
  storageBucket: "dobraya-ovechka.appspot.com",
  messagingSenderId: "698251130704"
}
let app = Firebase.initializeApp(config)
let db = app.database()
let booksRef = db.ref('books')
let auth = Firebase.auth()

export default {
  name: 'app',
  firebase: {
    books: booksRef
  },
  components: {
    PulseLoader,
    OrdersBag
  },
  data () {
    return {
      newBook: {
        title: '',
        author: '',
        url: 'http://'
      },
      headerNavOpened: false,
      headerFixed: false,
      headerMobileShown: true,
      pageLoaded: false,
      loading: true,
      orderItem: {},
      bagItems: []
    }
  },
  methods: {
    login: function(e) {
      e.preventDefault();
      auth.signInWithEmailAndPassword('birmudo@gmail.com', 'dobrayaovechka')
        .then(function(res) {
          console.log('Logged in', res);
        })
        .catch(function(error) {
          var errorCode = error.code;
          var errorMessage = error.message;
          console.log(errorMessage);
        });
    },
    logout: function(e) {
      e.preventDefault();
      auth.signOut().then(function(res) {
        console.log('Logged out', res);
      }).catch(function(error) {
        console.log(error);
      });
    },
    toggleHeaderNav: function() {
      this.headerNavOpened = !this.headerNavOpened;
    },
    addBook: function() {
      booksRef.push(this.newBook);
      this.newBook.title = '';
      this.newBook.author = '';
      this.newBook.url = 'http://';
    },
    removeBook: function(book) {
      booksRef.child(book['.key']).remove()
      toastr.success('Book removed successfully')
    },
    submitItemToBag: function(orderItem) {
      this.orderItem = orderItem
      let testArrayToUniq = function(element) {
        return element.color === orderItem.color && element.size === orderItem.size
      };
      if (!this.bagItems.some(testArrayToUniq)) {
        this.bagItems.push(this.orderItem)
      } else {
        _.forEach(this.bagItems, (item, index) => {
          if (item.color === orderItem.color && item.size === orderItem.size) {
            if ((item.amount + orderItem.amount) <= 10) {
              item.amount = item.amount + orderItem.amount
            } else {
              this.displayNotification()
            }
            this.bagItems[index].amount = item.amount
          }
        })
      }
      localStorage.setItem('orderBagState', JSON.stringify(this.bagItems))
      this.bagItems = JSON.parse(localStorage.getItem('orderBagState'))
    },
    displayNotification() {
      this.$snotify.warning(
        'Если вы действительно хотите заказать больше 10 - звоните нам +7 913 150-10-47',
        'Лимит 10', {timeout: 3500}
      )
    }
  },
  computed: {
    headerToggler: function () {
      return {
        'navbar-opened': this.headerNavOpened === true
      }
    },
    dateYear: function() {
      return new Date().getFullYear();
    },
    ifPageLoaded: function() {
      return {
        'page-loaded': this.pageLoaded === true
      }
    },
    bagFull: function() {
      return this.bagItems.length
    },
  },
  updated() {
    localStorage.removeItem('orderBagState')
    localStorage.setItem('orderBagState', JSON.stringify(this.bagItems))
  },
  created() {
    this.bagItems = JSON.parse(localStorage.getItem('orderBagState')) || []
  },
  beforeDestroy() {
    localStorage.removeItem('orderBagState')
  }
}
</script>


