// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import SvgSprite from 'vue-svg-sprite'
import VueFire from 'vuefire'
import App from './App'
import router from './router'
import VueInstagram from 'vue-instagram'
import VueLodash from 'vue-lodash'
import Snotify from 'vue-snotify'
import 'vue-snotify/styles/material.css'

Vue.use(VueFire)
Vue.use(SvgSprite, {
  url: './static/img/homepage-icons.svg',
  class: 'svg-icon'
})
Vue.use(VueInstagram)
Vue.use(VueLodash)
Vue.use(Snotify)

Vue.config.productionTip = false

Vue.mixin({
  data: function() {
    return {
      dropdownOpened: false,
      bagAnimationShown: true,
      paletteOpened: false
    }
  },
  methods: {
    headerBehaviourOnScroll: function(e) {
      var currentScroll = window.pageYOffset;
      if (window.pageYOffset > 46) {
        this.headerFixed = true;
      } else this.headerFixed = false;
    },
    scrollToTop: function() {
      window.scrollTo ({top: 0, left: 0, behavior: 'smooth'})
    },
    scrollToElement: function(id) {
      let element = document.getElementById(id)
      window.scrollTo ({top: element.offsetTop, left: 0, behavior: 'smooth'})
    },
    dropdownOpen: function() {
      this.dropdownOpened = true
    },
    dropdownClose: function() {
      this.dropdownOpened = false
    }
  },
  created () {
    document.addEventListener('scroll', this.headerBehaviourOnScroll)
    window.addEventListener('load', () => {
      this.pageLoaded = true
    })
  },
  destroyed () {
    document.removeEventListener('scroll', this.headerBehaviourOnScroll);
  }
})

/* eslint-disable no-new */
new Vue({
  render: h => h(App),
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



