<template>
<div class="page infopage about-us">
  <section class="section showonload">
    <div class="container">
      <h1>О проекте</h1>
      <div class="row row-main">
        <div class="col col-l-12">
          <p>
            Рада видеть Вас у нас 🤗 Это территория, свободная от неуюта, антистиля и недокомфорта.<br class="desktop-only">
            Этот проект возник из моей любви к постоянной работе над пространством, которое меня окружает.<br class="desktop-only">
            Я постоянно придумываю, что бы ещё сделать дома такого, чтобы стало ещё удобнее и уютнее.
          </p>
        </div>
      </div>
      <div class="row row-main">
        <div class="col col-s-12 col-l-6">
          <h2>Что мне близко и вдохновляет меня:</h2>
          <ul>
            <li>
              Лаконичный дизайн;
            </li>
            <li>
              Интересные фактуры и их сочетание;
            </li>
            <li>
              Пастельные, сложные оттенки.
            </li>
          </ul>

          <h2>Что для меня важно:</h2>
          <p>
            <ul>
              <li>
                <strong>Индивидуальный подход.</strong>
                <p>
                  Вы всегда можете написать мне, если не нашли здесь то, о чем мечтаете.
                  Я всегда стараюсь идти на встречу и воплощать Ваши мечты.
                </p>
              </li>
              <li>
                <strong>Многофункциональность.</strong>
                <p>
                  Большинство вещей, которые я делаю, можно использовать не в одной ситуации, а это значит, что они станут любимыми и не будут лежать без дела.
                </p>
              </li>
              <li>
                <strong>Гармония.</strong>
                <p>
                  Все изделия, которые Вы здесь найдете, созданы как коллекция. Они отлично сочетаются между собой и дополняют друг друга.
                </p>
              </li>
              <li>
                <strong>Экономия времени и удовольствие от покупки.</strong>
                <p>
                  Вам не нужно ездить по разным магазинам, тратить время в пробках и возвращаться с пустыми руками.
                  И я всегда рада поболатать, подробно рассказать, помочь с выбором.
                </p>
              </li>
              <li>
                <strong>Ваше возвращение.</strong>
                <p>
                  Мне очень приятно, когда Вы возвращаетесь за новыми покупками. И чтобы поблагодарить Вас за это, я дарю скидку 10% на следующую покупку. Вам нужно только оставить отзыв.
                </p>
              </li>
            </ul>
          </p>
        </div>
        <div class="col col-s-12 col-l-6">
          <img class="fullwidth-image" src="../assets/images/about-us.jpg" alt="">
          <p>
            Мне очень хочется, чтобы каждый человек, возвращаясь домой, возвращался именно в Свой Дом, где каждая вещь особенна, важна, качественна и создаёт неповторимую атмосферу.
          </p>
        </div>
      </div>
      <h2 class="conclusion">
        Приятных покупок!
      </h2>
    </div>
  </section>

  <transition name="fade">
    <div v-if="!ifPageLoaded">
      <pulse-loader :loading="loading" :color="'#546b84'" :size="'12px'"
                    class="loading-spinner"></pulse-loader>
    </div>
  </transition>

</div>
</template>


<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Product',
  props: ['pageLoaded'],
  components: {
    PulseLoader
  },
  data () {
    return {
      loading: true
    }
  },
  mounted () {
    this.scrollToTop()
    this.loading = false
  },
  computed: {
    ifPageLoaded: function() {
      return this.pageLoaded === true
    }
  }
}
</script>



