<template>
<transition name="modal">
  <div @click="$emit('close', bagItems)"
       class="modal-mask submit-orders-form">
    <div class="modal-wrapper modal-double-form">
      <div class="modal-container">
        <div class="modal-header">
          <slot name="header"></slot>
        </div>
        <div @click.stop="$emit('stop')" class="modal-body">
          <slot name="body">
            <div class="row modal-form">

              <div class="col col-l-6">
                <h2>Ваш заказ</h2>
                <div class="bag-items">
                  <transition-group name="list" tag="div">
                    <div v-for='(bagItem, index) in bagItems' :key='index'
                        class="list-item bag-item">
                      <div v-if="bagItem.photo"
                           :style="{ backgroundImage: 'url('
                                    + `${ require('../assets/images/products/' + bagItem.productId
                                    + '/' + bagItem.photo) }` + ')' }"
                          class="bag-item-photo">
                      </div>
                      <div class="bag-item-details">
                        <div class="bag-item-name">
                          {{ bagItem.name }}
                        </div>
                        <div class="bag-item-size">
                          {{ bagItem.size }}
                        </div>
                        <div class="bag-item-color">
                          {{ bagItem.color }}
                        </div>
                        <div class="quantity-remove">
                          <span class="quantity">
                            <input type="number" class="quantity-value-field"
                                  min="1" max="10" step="1">
                            <a @click="bagItem.amount--"
                              :class="{ 'disabled': bagItem.amount <= 1 }"
                              class="quantity-button quantity-button-down">
                              -
                            </a>
                            <span class="quantity-value">
                              {{ bagItem.amount }}
                            </span>
                            <a @click="bagItem.amount++"
                              :class="{ 'disabled': bagItem.amount >= 10 }"
                              class="quantity-button quantity-button-up">
                              +
                            </a>
                          </span>
                          <span @click='removeBagItem(index)'
                                class="remove-bag-item-button mobile-only">
                            <svg v-svg symbol="icon-remove-item" size="13 13"></svg>
                          </span>
                          <span @click='removeBagItem(index)'
                                class="remove-bag-item-button desktop-only">
                            <svg v-svg symbol="icon-remove-item-big" size="16 16"></svg>
                          </span>
                        </div>
                      </div>
                      <div class="bag-item-price">
                        {{ bagItem.priceInit * bagItem.amount }} ₽
                      </div>
                    </div>
                  </transition-group>
                </div>

                <div class="bag-total">
                  Общая сумма:
                  <span class="bag-item-price">
                    {{ totalCost }} ₽
                  </span>
                </div>
              </div>

              <div class="col col-l-6">
                <h2>Контакты</h2>
                <form v-on:submit="submitForm" action="https://formspree.io/dobraya-ovechka@mail.ru"
                      method="POST" class="footer-submit-form">
                  <input v-for='(bagItem, index) in bagItems'
                         :key='index'
                         v-model='bagItem.amount'
                         :name='bagItem.name + " " + bagItem.size + " " + bagItem.color'
                         class="bag-items-form-details"
                         type="text" >
                  <input v-model='totalCost'
                         name='Общая сумма заказа'
                         class="bag-items-form-details"
                         type="text">
                  <div class="modal-form-row">
                    <label for="" class="modal-form-label">
                      Имя и фамилия
                    </label>
                    <input v-model='name' id="name" type="text"
                           class="modal-form-value" name="Имя и фамилия">
                  </div>
                  <div class="modal-form-group">
                    <div class="modal-form-group-row">
                      <label for="" class="modal-form-label">
                        Телефон
                      </label>
                      <input v-model='phone' id="phone" type="text"
                             class="modal-form-value" name="Телефон">
                    </div>
                    <div class="modal-form-group-row">
                      <label for="" class="modal-form-label">
                        Электронная почта
                      </label>
                      <input v-model='email' id="email" type="text"
                             class="modal-form-value" name="Электронная почта">
                    </div>
                  </div>
                  <div class="modal-form-row">
                    <label for="" class="modal-form-label">
                      Адрес доставки, индекс
                    </label>
                    <textarea v-model='address'
                              cols="30" rows="4" id="address"
                              class="modal-form-value" name="Адрес доставки, индекс">
                    </textarea>
                  </div>
                  <div v-if='errors.length' class="modal-form-row-errors">
                    <transition-group name="list" tag="ul">
                      <li v-for="error in errors" :key='error'>{{ error }}</li>
                    </transition-group>
                  </div>

                  <br>
                  <div class="modal-form-group">
                    <div class="modal-form-group-row">
                      <label for="" class="modal-form-label">
                        Промокод
                      </label>
                      <input v-model='promocode' type="text"
                             class="modal-form-value" name="Промокод">
                    </div>
                    <div class="modal-form-group-row">
                      <button class="button form-button" type="submit">
                        Оформить заказ
                      </button>
                    </div>
                  </div>
                  <p class="modal-form-paragraph">
                    Я свяжусь с вами в течении часа и уточню детали заказа —
                    сроки изготовления, способ оплаты и стоимость доставки
                  </p>
                </form>
              </div>
              <span @click="$emit('close', bagItems)"
                    class="modal-close-button mobile-only">
                <svg v-svg symbol="icon-remove-item-big" size="16 16"></svg>
              </span>
            </div>
          </slot>
        </div>
        <div class="modal-footer">
          <slot name="footer"></slot>
        </div>
      </div>
    </div>
  </div>
</transition>
</template>


<script>
export default {
  name: 'OrdersFormModal',
  props: [
    'bagItemsTransferred'
  ],
  data() {
    return {
      totalSum: 0,
      bagItems: [],
      errors: [],
      name: null,
      phone: null,
      email: null,
      address: null,
      promocode: null
    }
  },
  watch: {
    bagItemsTransferred(array) {
      this.bagItems = array
    }
  },
  methods: {
    removeBagItem: function(i) {
      this.bagItems.splice(i, 1)
    },
    validEmail: function(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    submitForm: function(e) {
      this.errors = [];
      if (!this.name) {
        this.errors.push("Нужно ввести имя")
        document.getElementById("name").classList.add('with-error')
      } else document.getElementById("name").classList.remove('with-error')
      if (!this.phone) {
        this.errors.push("Нужно ввести телефонный номер")
        document.getElementById("phone").classList.add('with-error')
      } else document.getElementById("phone").classList.remove('with-error')
      if (!this.email) {
        this.errors.push("Нужно ввести емейл")
        document.getElementById("email").classList.add('with-error')
      } else if (!this.validEmail(this.email)) {
        this.errors.push("Введите пожалуйста корректный емейл")
        document.getElementById("email").classList.add('with-error')
      } else document.getElementById("email").classList.remove('with-error')
      if (!this.address) {
        this.errors.push("Нужно ввести адрес")
        document.getElementById("address").classList.add('with-error')
      } else document.getElementById("address").classList.remove('with-error')
      if (!this.errors.length) {
        this.$emit('close', this.bagItems)
        this.$snotify.warning (
          'Подтвердите пожалуйста что вы не робот на открывшейся странице :-)',
          'Почти всё', {timeout: 1800, position: 'centerCenter'}
        )
        setTimeout(function() {
          return true
        }, 2000)
      } else {
        e.preventDefault()
      }
    }
  },
  computed: {
    totalCost: function() {
      this.totalSum = 0
      _.forEach(this.bagItems, item => {
        this.totalSum = this.totalSum + item.priceInit * item.amount
      })
      return this.totalSum
    },
    bagFull: function() {
      return this.bagItems.length
    },

  },
  updated() {
    // console.log('updated hook');
    localStorage.removeItem('orderBagState')
    if (this.bagItems.length > 0) {
      localStorage.setItem('orderBagState', JSON.stringify(this.bagItems))
    }
  },
  created() {
    // console.log('created hook');
    this.bagItems = JSON.parse(localStorage.getItem('orderBagState')) || []
  },
  beforeDestroy() {
    // console.log('beforeDestroy hook');
    localStorage.removeItem('orderBagState')
  }

}
</script>



