<template>
<div v-if='bagFull'
     @mouseover='dropdownOpen()'
     :class='{ "opened": dropdownOpened }'
     class="popover popover-with-tail bag">
  <transition name="fade">
    <div v-if='dropdownOpened'
         @click='dropdownClose()'
         class="dropdown-overlay"></div>
  </transition>
  <div class="popover-trigger">
    <span class="popover-trigger-text">
      Корзина
    </span>
    <span class="bag-products-counter">
      <svg v-svg symbol="icon-bag" size="20 25"></svg>
      <span class="counter">
        {{ bagItems.length }}
      </span>
    </span>
    <div v-if='bagAnimationShown'
         @mouseover.stop='bagAnimationShown = false'
         class="dot-animation"></div>
  </div>
  <div class="popover-content">
    <span class="popover-content-tail"></span>
    <div class="bag-items">
      <transition-group name="list" tag="div">
        <div v-for='(bagItem, index) in bagItems' :key='index'
             class="list-item bag-item">
          <div v-if="bagItem.photo"
               class="bag-item-photo"
               :style="{ backgroundImage: 'url('
                        + `${ require('../assets/images/products/' + bagItem.productId
                        + '/' + bagItem.photo) }` + ')' }"
                        >
          </div>
          <div class="bag-item-details">
            <div class="bag-item-name">
              {{ bagItem.name }}
            </div>
            <div class="bag-item-size">
              {{ bagItem.size }}
            </div>
            <div class="bag-item-color">
              {{ bagItem.color }}
            </div>
            <div class="bag-item-color">
              {{ bagItem.pattern }}
            </div>
            <div class="quantity-remove">
              <span class="quantity">
                <input type="number" class="quantity-value-field"
                      min="1" max="10" step="1">
                <a @click="bagItem.amount--"
                  :class="{ 'disabled': bagItem.amount === 1 }"
                  class="quantity-button quantity-button-down">
                  -
                </a>
                <span class="quantity-value">
                  {{ bagItem.amount }}
                </span>
                <a @click="bagItem.amount++"
                  :class="{ 'disabled': bagItem.amount === 10 }"
                  class="quantity-button quantity-button-up">
                  +
                </a>
              </span>
              <span @click='removeBagItem(index)'
                    class="remove-bag-item-button">
                <svg v-svg symbol="icon-remove-item" size="13 13"></svg>
              </span>
            </div>
          </div>
          <div class="bag-item-price">
            {{ bagItem.priceInit * bagItem.amount }} ₽
          </div>
        </div>
      </transition-group>
    </div>

    <div class="bag-total">
      Общая сумма:
      <span class="bag-item-price">
        {{ totalCost }} ₽
      </span>
    </div>

    <div class="send-order-form">
      <a @click="openSubmitOrdersModal()"
        class="button button-primary section-button">
        Оформить заказ
      </a>
    </div>

  </div>

  <orders-form-modal v-if="showModal"
                     @close="modalFormClosed">
    <h3 slot="header"></h3>
  </orders-form-modal>

</div>
</template>


<script>
import OrdersFormModal from './OrdersFormModal'

export default {
  name: 'OrdersBag',
  props: [
    'bagItemsTransferred'
  ],
  components: {
    OrdersFormModal
  },
  data() {
    return {
      totalSum: 0,
      bagItems: [],
      showModal: false
    }
  },
  watch: {
    bagItemsTransferred(array) {
      this.bagItems = array
      if(this.bagItems.length) {
        this.bagAnimationShown = true
      }
    }
  },
  methods: {
    removeBagItem: function(i) {
      this.bagItems.splice(i, 1)
    },
    openSubmitOrdersModal: function() {
      this.showModal = true
    },
    modalFormClosed: function(currentBagItems) {
      this.bagItems = currentBagItems
      localStorage.setItem('orderBagState', JSON.stringify(currentBagItems))
      this.showModal = false
    },
    // consoleTest: function(el) {
    //   console.log(el)
    // }
  },
  computed: {
    totalCost: function() {
      this.totalSum = 0
      _.forEach(this.bagItems, item => {
        this.totalSum = this.totalSum + item.priceInit * item.amount
      })
      return this.totalSum
    },
    bagFull: function() {
      return this.bagItems.length
    }
  },
  updated() {
    // console.log('updated hook')
    localStorage.removeItem('orderBagState')
    if (this.bagItems.length > 0) {
      localStorage.setItem('orderBagState', JSON.stringify(this.bagItems))
    }
  },
  created() {
    // console.log('created hook')
    this.bagItems = JSON.parse(localStorage.getItem('orderBagState')) || []
  },
  beforeDestroy() {
    // console.log('beforeDestroy hook')
    localStorage.removeItem('orderBagState')
  }
}
</script>



