<template>
<div class="page infopage">
  <section class="section showonload">
    <div class="container">
      <h1>Как заказать. Оплата. Доставка.</h1>

      <h2>Как заказать.</h2>
      <p>
        Чтобы положить в корзину понравившиеся вещи, выберите размер, количество, цвет, рисунок и нажмите кнопку "Добавить в корзину".
      </p>
      <p>
        Выбранный товар появится в корзине в верхнем правом углу. Если вы хотите несколько товаров из одной категории, но разных цветов и размеров, добавьте каждый из них в корзину по-отдельности.
      </p>
      <p>
        Затем перейдите в Корзину, нажмите кнопку "Оформить заказ" и заполните появившиеся поля.
      </p>
      <p>
        Если у вас есть промокод, впишите его в специальную строку при оформлении заказа в корзине.
      </p>
      <p>
        После оформления заказа я свяжусь с вами в течение часа и уточню детали, сообщу реквезиты для оплаты.
      </p>

      <br>

      <h2>Оплата.</h2>
      <p>
        Я работаю по 100% предоплате. Работа по выполнению заказа начинается сразу после его оплаты.
      </p>
      <p>
        Оплату можно произвести любым удобным для вас способом:
        <ul>
          <li>
            на карту Сбербанка
          </li>
          <li>
            на кошелек Яндекс.Деньги
          </li>
          <li>
            на карту Tinkoff
          </li>
          <li>
            на Epayments
          </li>
        </ul>
      </p>
      <p>
        Реквизиты я сообщу при оформлении заказа.
      </p>

      <br>

      <h2>Доставка.</h2>

      <p>
        Я отправляю заказы по России и зарубеж.
      </p>
      <p>
        При оформлении заказа я сообщу вам точную стоимость доставки почтой (т.к. учитывается только вес) и ориентировочную стоимость доставки транспортными компаниями (т.к. кроме веса учитываются габариты, а они всегда разные и всегда отличаются от тех, что измеряют в ТК), и вы выберете наиболее удобный способ доставки. Обычно стоимость доставки от 300 до 1000 руб., но всё индивидуально. При выборе не забывайте учитывать часы работы ТК, удаленность пунктов самовывоза от дома.
      </p>
      <p>
        По умолчанию доставка осуществляется до ближайшего к вам отделения Почты России или пункта самовывоза транспортной компании. Возможна курьерская доставка до дверей по согласованию.
      </p>
      <p>
        Оплата доставки Почтой России осуществляется одновременно с оплатой заказа на карту Сбербанка или Яндекс.Деньги.
      </p>
      <p>
        Оплата доставки ТК осуществляется при получении.
      </p>
      <p>
        ТК, которым я доверяю, и с которыми сотрудничаю: СДЭК, ПЭК, Энергия, Желдорэкспедиция.
      </p>
      <p>
        Для оформления доставки Почтой России необходима следующая информация: ФИО, область, город, улица, дом, квартира, индекс.
      </p>
      <p>
        Для оформлении доставки ТК: ФИО, город, адрес пункта самовывоза, если их несколько, номер телефона. При отправке Желдорэкспедицией - паспортные данные.
        После отправки я сообщу вам идентификационный номер для отслеживания посылки.
      </p>
      <p>
        Как только посылка прибудет, вы получите извещение от Почты России или с вами свяжутся работники ТК.
      </p>
      <p>
        При получении посылки необходимо при себе иметь паспорт. Забирать посылку можете только лично вы. Если вы знаете, что не сможете забрать самостоятельно, то доставку можно оформить на другого человека.
      </p>
      <p>
        Сроки доставки от 2 дней, точнее можно узнать, написав мне, или на сайте ТК (доставка из Омска). Возможен бесплатный самовывоз в Омске или доставка по городу - 100 руб.
      </p>
      <p>
        Международная доставка осуществляется по тем же правилам вышеперечисленными транспортными компаниями, если они имеют представительства в вашей стране, или в любую страну международной доставкой EMS.
      </p>
      <p>
        Бесплатная доставка для подписчиков моей почтовой рассылки осуществляется только по России. Я самостоятельно выбираю способ доставки и информирую вас о нем. Бесплатная доставка возможна только до отделения Почты России или пункта самовывоза ТК.
      </p>
    </div>
  </section>

  <transition name="fade">
    <div v-if="!ifPageLoaded">
      <pulse-loader :loading="loading" :color="'#546b84'" :size="'12px'"
                    class="loading-spinner"></pulse-loader>
    </div>
  </transition>

</div>
</template>


<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Product',
  props: ['pageLoaded'],
  components: {
    PulseLoader
  },
  data () {
    return {
      loading: true
    }
  },
  mounted () {
    this.scrollToTop()
    this.loading = false
  },
  computed: {
    ifPageLoaded: function() {
      return this.pageLoaded === true
    }
  }
}
</script>



