<template>
<div class="page infopage">
  <section class="section showonload">
    <div class="container">
      <h1>Контакты</h1>

      <h2>Адрес</h2>
      <p>
        г. Омск, ул. Лукашевича, д. 14/3
      </p>
      <br>

      <h2>WhatsApp, Telegram, Viber</h2>
      <p>
        <a href="tel:79131501047">
          <svg viewBox="0 0 18 18" width="18" height="18" class="svg-icon">
            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="./static/img/homepage-icons.svg#icon-phone"></use>
          </svg>
          <span class="link-text">
            +7 913 150-10-47
          </span>
        </a>
      </p>
      <br>

      <h2>Емейл</h2>
      <p>
        <a href="mailto:dobraya-ovechka@mail.ru">
          dobraya-ovechka@mail.ru
        </a>
      </p>
      <br>

      <h2>Инстаграм</h2>
      <p>
        <a target="_blank" href="https://www.instagram.com/dobraya_ovechka/">
          dobraya_ovechka
        </a>
      </p>
      <br>

      <h2>ВКонтакте </h2>
      <p>
        <a target="_blank" href="https://vk.com/dobrayaovechka">
          Добрая Овечка
        </a>
      </p>

      <br><br>
      <h2 class="conclusion">
        Я рада общению с вами всегда!
      </h2>

    </div>
  </section>

  <transition name="fade">
    <div v-if="!ifPageLoaded">
      <pulse-loader :loading="loading" :color="'#546b84'" :size="'12px'"
                    class="loading-spinner"></pulse-loader>
    </div>
  </transition>

</div>
</template>


<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Product',
  props: ['pageLoaded'],
  components: {
    PulseLoader
  },
  data () {
    return {
      loading: true
    }
  },
  mounted () {
    this.scrollToTop()
    this.loading = false
  },
  computed: {
    ifPageLoaded: function() {
      return this.pageLoaded === true
    }
  }
}
</script>



